import { 
  getUserById,
  getPrescriptionsByStatus,
  getPrescriptionDetailsByPrescriptionId,
  getMedicineById,
  getAllInvoices,
  getAllPrescriptions,
  User,
  Prescription,
  PrescriptionDetail
} from '../datats/mockPatients';

// Type definitions to match the pharmacy page UI needs
export interface PharmacyPatient {
  id: string;
  serialNumber: string;
  fullName: string;
  age?: number; // Optional since we might not have age data
  gender?: string; // Optional since we might not have gender data
  phone: string;
  address?: string; // Optional since we might not have address data
  medicalHistory?: string;
  diagnosis: string;
  doctor: string;
  prescription: PharmacyMedicine[];
}

export interface PharmacyMedicine {
  name: string;
  quantity: number;
  dosage: string;
  price: number;
}

export interface PharmacyInvoice {
  id: string;
  date: string;
  patientId: string;
  pharmacistName: string;
  totalAmount: number;
}

// Function to get patients with pending prescriptions for the pharmacy
export const getPatientsWithPendingPrescriptions = (): PharmacyPatient[] => {
  // Get all prescriptions with status PENDING_DISPENSE
  const pendingPrescriptions = getPrescriptionsByStatus('PENDING_DISPENSE');
  
  if (!pendingPrescriptions.length) return [];
  
  // Map prescriptions to pharmacy patient format
  return pendingPrescriptions.map((prescription, index) => {
    const patient = getUserById(prescription.patientId);
    const doctor = getUserById(prescription.doctorId);
    const prescriptionDetails = getPrescriptionDetailsByPrescriptionId(prescription._id);
    
    if (!patient) return null;
    
    // Map medicines to pharmacy medicine format
    const pharmacyMedicines = prescriptionDetails.map(detail => {
      const medicine = getMedicineById(detail.medicineId);
      return {
        name: medicine ? medicine.name : 'Unknown medicine',
        quantity: detail.quantity,
        dosage: detail.dosage,
        price: medicine ? medicine.price : 0
      };
    });
    
    return {
      id: patient._id,
      serialNumber: prescription.customPrescriptionId || `P${index + 1}`,
      fullName: patient.fullName,
      phone: patient.phone,
      diagnosis: prescription.diagnosis,
      doctor: doctor ? doctor.fullName : 'Không xác định',
      prescription: pharmacyMedicines
    };
  }).filter(Boolean) as PharmacyPatient[];
};

// Function to get pharmacy invoice data
export const getPharmacyInvoices = (): PharmacyInvoice[] => {
  const invoices = getAllInvoices();
  const pharmacistId = '685face13fc4c04e1bd96c0a'; // ID của dược sĩ Phạm Văn Dũng
  
  return invoices.map(invoice => {
    const pharmacist = getUserById(pharmacistId);
    
    return {
      id: invoice._id,
      date: invoice.createdAt,
      patientId: invoice.patientId,
      pharmacistName: pharmacist ? pharmacist.fullName : 'Nguyễn Thị Hà',
      totalAmount: invoice.totalAmount
    };
  });
};

// Note: Since we don't have date of birth in our User model, this function is removed
// We'll use direct age values if/when we add them to the User model

// Function to get daily revenue data for statistics
export const getDailyRevenue = () => {
  const invoices = getAllInvoices();
  
  // Group invoices by date
  const revenueByDate = invoices.reduce((acc, invoice) => {
    const date = new Date(invoice.createdAt).toISOString().split('T')[0];
    
    if (!acc[date]) {
      acc[date] = 0;
    }
    
    acc[date] += invoice.totalAmount;
    return acc;
  }, {} as Record<string, number>);
  
  // Convert to array format for UI
  return Object.entries(revenueByDate).map(([date, amount]) => ({
    date,
    amount
  }));
};

// Function to get invoices for a specific date
export const getInvoicesForDate = (date: string) => {
  const invoices = getAllInvoices();
  
  return invoices.filter(invoice => {
    const invoiceDate = new Date(invoice.createdAt).toISOString().split('T')[0];
    return invoiceDate === date;
  });
};
